.side-drawer {
    
    /*background: white;*/
    
    position:absolute;
    overflow:hidden;
    top:0;
    left:0;
    width:100%; 
    padding:0;
    z-index: 9997;
    
}

.side-drawer ul {
    list-style:none;
    display:flex;
    flex-direction: column;
    padding: 0 10% 0 10%;
    padding-top: 1.875em;
    margin:0;

}


.side-drawer a {
    color: #808080;
    font-size:1;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
      
}

.side-drawer a:hover ,
.side-drawer a:active {
    color:rgba(0,0,0, 0.5);
}

.side-drawer__items {
    padding-top:60px!important;
}

.side-drawer__items li{
    padding: 30px 0 30px;
    line-height: 2;
    border-bottom: 1px solid #a2a2a2;   
}
    

.side-drawer__brand li{
    width:94%;
}
.side-drawer__brand a{
    font-size:0.85em;
    letter-spacing: 0.04em;
}

.Eklov {
    margin-top:2.5rem;
    margin-bottom: 4rem;
    width:200px;
    margin-left:auto;
    margin-right:auto;
    
}

@media (min-width:1025px){
    .side-drawer{
        
        /*display:none;*/
    }
    li{
        text-align:center;
    }

    
    .side-drawer ul {
       
        align-items: center;
    }


    .side-drawer__items li {
        width:40rem;
        
    }

    .side-drawer__brand li{
        width:100%;
        margin-top:50px;
    }

}